import { Autocomplete, Stack, TextField } from "@mui/material";
import { useState } from "react";

export const MultiAutocomplete = ()=>{

    const data= ["javascript","python","html","css","java","vbscript"];
    const [skill,setSkill] = useState("");
    const [skills,setSkills] = useState([]);
    console.log(skill);
    console.log(skills);
    return (
        <Stack spacing={2} width="250px">
            <Autocomplete 
                options={data}
                renderInput={(param)=> <TextField {...param} label="Skills" />}
                value={skill}
                onChange={(e,newValue)=>setSkill(newValue)}   // e.target.valueAutocompleteentervalue
                freeSolo         // 
                
            />


            <Autocomplete 
                options={data}
                renderInput={(param)=> <TextField {...param} label="Skills" />}
                value={skills}
                onChange={(e,newValue)=>setSkills(newValue)}
                freeSolo         //  
                multiple         // 
                filterSelectedOptions  //
            />
            
        </Stack>

    );
}


