import { Box } from "@mui/material";

export const MultiBox = ()=>{
    return (
        <>
            {/* componenthtml tagdiv span article section */}
            <Box component="span">test1</Box>

            {/* propssxstylecss */}
            <Box component="span" sx={{
                backgroundColor:"primary.main",
                color:"white",
                width:"100px",
                height:"100px",
                padding:"16px",
                border: 5,
                borderColor: 'secondary.main',
                ':hover':{
                    backgroundColor:"primary.light"
                }
            }}>test2</Box>


            {/* Boxhtmlcss */}
            <Box display="flex" width="100px" height="100px" p={2} bgcolor="success.main" border={2} >test3</Box>

            <Box>
                <Box sx={{
                    width:"100px",
                    height:"100px",
                    backgroundColor:"error.main",
                    display:{
                        xs:"block",
                        md:"inline-block"
                    }
                }}>test4</Box>
                <Box sx={{
                    width:"100px",
                    height:"100px",
                    backgroundColor:"info.main",
                    display:{
                        xs:"block",
                        md:"inline-block"
                    }
                }}>test5</Box>
            </Box>
        </>
    );
}

